import React, { useState } from 'react';
import styled from 'styled-components';
import { theme } from './Variables';
import Styles from './Styles.module.css';
import { IconButton } from './Buttons/IconButton';
import { MdAccountCircle, MdExitToApp } from 'react-icons/md';
import { Menu, MenuItem, MenuItemText, MenuItemIcon } from './Menu';

const AppBarWrapper = styled.div.attrs({className: Styles.elevation1})`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: ${theme.palette.primary.main};
    padding: 0 1rem;
`;

const ContentLeft = styled.div`

`;

const ContentRight = styled.div`

`;

const AppBar = function(props) {

    const [showAccountMenu, setShowAccountMenu] = useState(false);
    return (
        <AppBarWrapper>
            <ContentLeft>
                <span style={{fontWeight: '700', fontSize: '1.5rem', color: '#fff'}}>Logo</span>
            </ContentLeft>
            <ContentRight>
            <IconButton 
                variant="text" 
                size="45px"
                color="primary"
                onClick={() => setShowAccountMenu(!showAccountMenu)}
            >
                <MdAccountCircle size="1.5rem" color="#fff" /></IconButton>
                { 
                    showAccountMenu 
                        ? 
                            <Menu bottom="0" right="1rem" >
                                <MenuItem onClick={() => { window.location = '/logout'}}>
                                    <MenuItemText>Sign Out</MenuItemText>
                                    <MenuItemIcon><MdExitToApp size="1rem" /></MenuItemIcon>
                                </MenuItem>
                            </Menu> 
                        : ''
                }
            </ContentRight>
        </AppBarWrapper>
    );
};

export { AppBar };